import { FC, useState } from "react";

import { Box, Button, Flex, HStack, Stack, Grid, GridItem, } from "@chakra-ui/react";
import { configs } from "shared/content/Content";
import { OtherProjectCard } from "pages/other-projects/other-project-card/OtherProjectCard";
import { ChevronDownIcon, ChevronUpIcon } from "utils/Icons";

const initialCount = 2;
const incrementor = 2;

export const OtherProjects: FC = () => {
    const [count, setCount] = useState<number>(initialCount);

    const scrollToElement = (idx: number) => {
        const elementTop = document
            .getElementById(`other-project-card-${configs.otherProjects[idx].id}`)
            ?.getBoundingClientRect().top;

        if (elementTop) {
            window.scrollTo({ top: elementTop + window.scrollY - 64, behavior: "smooth" });
        }
    };

    const onShowMore = () => {
        const oldCount = count;
        setCount(count + incrementor);
        setTimeout(() => {
            // scrollToElement(oldCount);
        }, 1);
    };

    const onShowLess = () => {
        setCount(initialCount);
        // scrollToElement(initialCount);
    };

    const ongoingProjects = configs.otherProjects.filter(p => p.status === "ongoing");
    const previousProjects = configs.otherProjects.filter(p => p.status === "previous");

    return (
        <Stack spacing={10}>
            <Box>
                <Box fontWeight="bold" fontSize="xl" mb={4}>Ongoing </Box>
                <Grid templateColumns={{ base: "1fr", md: "repeat(1, 1fr)", lg: "repeat(2, 1fr)" }} gap={6}>
                    {ongoingProjects.map((project) => (
                        <Box key={project.id}>
                            <OtherProjectCard {...project} />
                        </Box>
                    ))}
                </Grid>
            </Box>

            <hr></hr>

            <Box>
                <Box fontWeight="bold" fontSize="xl" mb={4}>Previous </Box>
                <Grid templateColumns={{ base: "1fr", md: "repeat(1, 1fr)", lg: "repeat(2, 1fr)" }} gap={6}>
                    {previousProjects.slice(0, count).map((project, idx) => (
                        <Box key={project.id} id={`other-project-card-${project.id}`}>
                            <OtherProjectCard {...project} />
                        </Box>
                    ))}
                </Grid>

                {previousProjects.length > initialCount && (
                    <Flex mt={4} justify="center">
                        {count < previousProjects.length ? (
                            <Button leftIcon={<ChevronDownIcon />} onClick={onShowMore}>
                                Show More
                            </Button>
                        ) : (
                            <Button leftIcon={<ChevronUpIcon />} onClick={onShowLess}>
                                Show Less
                            </Button>
                        )}
                    </Flex>
                )}
            </Box>
        </Stack>
    );
};
