import { FC, useState } from "react";
import {
  Box,
  Image,
  Badge,
  Flex,
  Heading,
  Text,
  Center,
  Button,
  VStack,
} from "@chakra-ui/react";
import { Tags } from "shared/tags/Tags";
import { ProjectCardFooter } from "shared/project-card-footer/ProjectCardFooter";

interface Props {
  id: string;
  title: string;
  demo?: string;
  github?: string;
  tags: string[];
  description: string;
  readMore?: string;
  image: string;
  jpg: string;
  year: string;
  recent: string;
  article?: string;
  figshare?: string;
}

export const OtherProjectCard: FC<Props> = ({
  id,
  title,
  demo,
  github,
  figshare,
  article,
  tags,
  description,
  readMore,
  image,
  jpg,
  year,
  recent,
}) => {
  const [expanded, setExpanded] = useState(false);

  return (
    <Flex
      alignItems={{ base: "flex-start", lg: "center" }}
      gap="10"
      id={`other-project-card-${id}`}
      py={{ base: "4", md: "4" }}
      
    >
      <Box
      
        borderWidth="1px"
        borderRadius="lg"
        overflow="hidden"
        boxShadow="md"
        
        _hover={{ boxShadow: "xl", transform: "translateY(-4px)" }}
        transition="all 0.2s ease-in-out"
      >
        <Center  bg="white" h="160px">
          <picture>
            <source type="image/webp" srcSet={image}></source>
            <source type="image/jpeg" srcSet={jpg}></source>
            <Image
              ignoreFallback
              src={image}
              borderRadius="xl"
              alt={`${title}-cover-image`}
            />
          </picture>
        </Center>

        <Box p="6">
          <Box display="flex" alignItems="baseline">
            {recent === "yes" && (
              <Badge borderRadius="full" px="2" mr="2" colorScheme="teal">
                New
              </Badge>
            )}
            <Box
              color="gray.500"
              fontWeight="semibold"
              letterSpacing="wide"
              fontSize="xs"
              textTransform="uppercase"
            >
              {year}
            </Box>
          </Box>

          <Box mt="2" mb="2" fontWeight="semibold" as="h4" lineHeight="tight">
            <Heading fontSize="lg" >{title}</Heading>
          </Box>

          <Box>
            <Text mt="2" mb="0" lineHeight="normal" noOfLines={expanded ? undefined : 3}>
              {description}
            </Text>
            <Flex justify="flex-end" mt={0} mb={2}>
            <Button 
              size="xs"
              variant="link"
              colorScheme="blue"
              onClick={() => setExpanded(!expanded)}
              mt={0}
              mb={2}
            >
              {expanded ? "Show less ˄" : "Show more ⌄"}
            </Button>
            </Flex>

            <Tags tags={tags} id={id} size="xs" />
          </Box>

          <ProjectCardFooter
            readMore={readMore}
            github={github}
            demo={demo}
            article={article}
            figshare={figshare}
          />
        </Box>
      </Box>
    </Flex>
  );
};
